import Vue from 'vue';
import Router from 'vue-router';
import page1 from '@/views/page1'





Vue.use(Router);

export default new Router({
  routes: [
    {
      path: '/',
      redirect:'page1'
    },{

      path: '/page1',
      name: 'page1',
      component: page1,

    }


  ],


});



