<template>
  <div id="app" >

    <router-view/>
  </div>
</template>

<script>

export default {
  name: 'App',
  data(){
    return {
      isPc :false
    }
  },

  created(){
    this.isPc = navigator.userAgent.toLowerCase().match(/(ipod|ipad|iphone|android|coolpad|mmp|smartphone|midp|wap|xoom|symbian|j2me|blackberry|wince)/i) == null;
  }

};


</script>

<style>
@import 'common/css/reset.css';
@import 'common/css/common.css';
@import 'assets/font/iconfont.css';

</style>
<style scoped>
#app{
  height: 100%;
  width: 100%;
  position: relative;


}


</style>




