import Vue from 'vue';
import axios from 'axios';
import { Toast } from 'mint-ui';
import md5 from '../lib/md5'

import state from './state';

Vue.prototype.$ELEMENT = { size: 'small' };

const prefix = 'http://120.79.12.201';

const  md5Salt = 'cn853oEhQAZ9Z4VE'
// api列表
const apiList = {

 'getKlineHistoryList' : prefix+'/app/v1/data/getKlineHistoryList',
 'saveUserPushData' : prefix+'/app/v1/push/saveUserPushData',

};


// axios.defaults.timeout = 15000;

axios.create({
  timeout: 10000,
});

const headers = {
  // 'Content-Type':'application/x-www-form-urlencoded'
  RequestSource: '3',
};

const responseHandler = (response) => {
  console.log('response11------>',response.data)

  if (response.data.code !== 0) {
    console.error('ajax response error', response.data.message, state[response.data.code]);
    return response;
  }
  return response;
};

const errorHandler = (error) => {
  if (error.message !== 'repeat') {
    console.error('ajax response error', error);
  }
  return Promise.reject(error);
};

const req = {};
const requestHandler = (config) => {
  if (config.abort) {
    config.cancelToken = new axios.CancelToken((c) => {
      const { url } = config;
      if (req[url]) {
        req[url]('repeat');
        delete req[url];
      }
      req[url] = c;
    });
  }
  config.params = config.params || {};
  config.params.t = (new Date()).valueOf();
  config.headers = Object.assign({}, config.headers, headers);
  if (headers.Authorization && /MSIE 9/i.test(navigator.appVersion)) {
    config.params.Authorization = headers.Authorization;
  }
  return config;
};


axios.interceptors.request.use(requestHandler);
axios.interceptors.response.use(responseHandler, errorHandler);


// 全局ajax请求
const getApi = (type, url, params, callback, errCallback) => {
  if (type === 'get') {
    params.t = new Date().getTime(); // 带时间戳，阻止缓存
    return axios.get(url, {
      params,
    }).then((response) => {

      console.log('response------>',response)
      if (response.data.code === 0) {
        if (typeof callback === 'function') {
          callback(response.data.data, response.data.message);
        }
      } else if (typeof errCallback === 'function') {
        errCallback(response.data);
      }
    }).catch((e) => {

      Toast('系统异常');
      console.error(e);
      if (typeof errCallback === 'function') {
        errCallback('系统异常');
      }
    });
  }
  if (type === 'post') {
    return axios.post(url, params).then((response) => {
      if (response.data.code === 0) {
        if (typeof callback === 'function') {
          callback(response.data.data, response.data.message);
        }
      }else if (typeof errCallback === 'function') {
        errCallback(response.data);
      }
    }).catch((e) => {

      console.error(e+"")
      Toast('系统异常');
      if (typeof errCallback === 'function') {
        errCallback('系统异常');
      }
    });
  }
  return 'error';
};



String.format = function() {
  var s = arguments[0];
  for (var i = 0; i < arguments.length - 1; i++) {
    var reg = new RegExp("\\{" + i + "\\}", "gm");
    s = s.replace(reg, arguments[i + 1]);
  }
  return s;
}


function signParam(param) {
  let sortKey = Object.keys(param).sort();
  let sortParam = {};
  sortKey.forEach(key => {
    sortParam[key] = param[key];
  })

  let sortParamStr = Object.keys(sortParam).map(function (key) {
    // body...
    return key + "=" + sortParam[key];
    // return encodeURIComponent(key) + "=" + encodeURIComponent(sortParam[key]);
  }).join("&");
  sortParamStr += md5Salt;
  console.log(sortParamStr)
  sortParam['sign'] = md5.hex_md5(sortParamStr);
  return sortParam;
}

const getKlineHistoryList = function (param,callback) {
  let url = apiList['getKlineHistoryList'];
  let sortParam = signParam(param);

  getApi('post', url, sortParam, callback, (data) => {
    console.log(data);
    Toast(data.message)
  });
};
const saveUserPushData = function (param,callback) {
  let url = apiList['saveUserPushData'];
  let sortParam = signParam(param);

  getApi('post', url, sortParam, callback, (data) => {
    console.log(data);
    Toast(data.message)
  });
};


export default { getApi,getKlineHistoryList ,saveUserPushData};



