<template>

  <div class="c-content">

    <div class="c-header">

      <div class="c-div-row">
        <span class="c-span_large">{{instrument.replace("#","")}}</span>
      </div>
      <div class="c-div-row">
        <span class="c-span_large">分时</span>
        <span class="c-span_middle" v-for="item in maData" :class="{ 'c-span_large-select': item.name === activeM }"
              @click="onMChange(item)">{{item.name}}</span>
      </div>
      <div class="c-div-row">
        <span class="c-span_price">{{kprice}}</span><span class="c-push" @click="showPopup(true)">推送<van-icon
        name="arrow-up"/></span>
      </div>
    </div>
    <div ref="kcontent" class="K-line" v-show="true"></div>
    <div class="c-indicator">
      <span class="c-span_middle" :class="{ 'c-span_large-select': isShowMA }" @click="isShowMA = !isShowMA">MA</span>
      <span class="c-span_middle" :class="{ 'c-span_large-select': isShowBOLL }"
            @click="isShowBOLL = !isShowBOLL">BOLL</span>
      <span class="c-span_middle" :class="{ 'c-span_large-select': isShowMACD }"
            @click="isShowMACD = !isShowMACD">MACD</span>
      <span class="c-span_middle" :class="{ 'c-span_large-select': isShowKDJ }"
            @click="isShowKDJ = !isShowKDJ">KDJ</span>
      <span class="c-span_middle" :class="{ 'c-span_large-select': isShowRSI }"
            @click="isShowRSI = !isShowRSI">RSI</span>
      <span class="c-span_middle" :class="{ 'c-span_large-select': isShowVOLUME }"
            @click="isShowVOLUME = !isShowVOLUME">VOLUME</span>
      <span class="c-span_middle" :class="{ 'c-span_large-select': isShowOBV }"
            @click="isShowOBV = !isShowOBV">OBV</span>
    </div>
    <div class="c-popou" v-show="isShowPopup" @click="showPopup(false)">

      <div class="c-popou-content">
        <ul>
          <li v-for="item in  maData">

            <div style="padding: 2px 4px">
              <span>{{item.name}}</span>
              <van-switch @click.stop size="12px" style="float: right" :value="item.name === switchOpenName" @input="(e)=>{
              onInput(item,e)
              }"></van-switch>
            </div>
          </li>
        </ul>
      </div>

    </div>
  </div>
</template>

<script>


  import echarts from "echarts";

  import kline from "../lib/k-line";
  import tmpData from "../lib/tmpData";
  import api from "../api/api";
  import ReconnectingWebSocket from "../lib/websocketUtil";


  export default {
    name: "page1",
    data() {
      return {


        userId: "47dafd5a4ab240aca89e2aac0a2e3a63",
        token: "52d80956b4bccbf9c0cc8ccef9c85021",
        instrument: "WTI#",
        isShowMA: false,
        isShowBOLL: false,
        isShowMACD: false,
        isShowKDJ: false,
        isShowRSI: false,
        isShowVOLUME: false,
        isShowOBV: false,
        isShowPopup: false,
        activeM: "M30",
        kprice:'-/-',
        dateType: 1,
        notInit: true,
        kData: [],
        ws: null,
        switchOpenName: "",

        kChart: null,

        maData: [
          {
            name: "M1",
            isAccept: true
          },
          {
            name: "M5",
            isAccept: true
          },
          {
            name: "M15",
            isAccept: true
          },
          {
            name: "M30",
            isAccept: true
          },
          {
            name: "H1",
            isAccept: true
          },
          {
            name: "H4",
            isAccept: true
          },
          {
            name: "D1",
            isAccept: true
          },
          {
            name: "W1",
            isAccept: true
          },
          {
            name: "MN",
            isAccept: true
          }
        ]
      };
    },


    methods: {

      onInput(e, checked) {

        console.log(e, checked);
        if (e.name === this.switchOpenName && !checked) {
          this.switchOpenName = "";
        } else {
          this.switchOpenName = e.name;
        }

      },

      onMChange(item) {

        if (item.name !== this.activeM) {
          this.activeM = item.name;
        }

      },

      showPopup(isShow) {

        this.isShowPopup = isShow;
      },
      onSocketMessage(e) {

        try {

          let pushData = JSON.parse(e.data).data[0];
          if (pushData.dateType === this.dateType && pushData['instrument'] === this.instrument) {

            this.kprice = pushData['openPrice']+"/"+pushData['closePrice']
            let lastBar = this.kData.slice(-1)[0];
            if (pushData["openDate"] > lastBar[0]) {
              this.kData = this.kData.concat([pushData]);
              this.kChart.setOption(kline.changeKDataOption(this.kData));
            } else if (pushData["openDate"] === lastBar[0]) { //如果相等，改变当前柱子

              this.kData.pop();
              this.kData = this.kData.concat([pushData]);
              this.kChart.setOption(kline.changeKDataOption(this.kData));
            }

          }

        } catch (ee) {
          console.error(ee);
        }

        //console.log(e);
      },

      onSocketEvent(e) {
        console.log(e);
      },getFirst() {

        if (this.kChart) {
          this.kChart.showLoading({
            text: 'loading',
            color: '#c23531',
            textColor: '#000',
            maskColor: 'rgba(0, 0, 0, 0.6)',
            zlevel: 0
          });
        }

        this.dateType = this.maData.map(item => {
          return item.name;
        }).indexOf(this.activeM) + 1;
        api.getKlineHistoryList({

          "instrument": this.instrument,
          "dateType": this.dateType,
          "id": "1",
          "pageSize": "280",
          "pageNo": "1",
          "userId": this.userId,
          "token": this.token,
          "language": "zh-cn"

        }, (data) => {

          if (this.kChart) {
            this.kChart.hideLoading();
          }
          this.kData = data.list.reverse().map(item => {
            return [item["openDate"], item["openPrice"], item["closePrice"], item["lowPrice"], item["highPrice"], item["volume"]];
          });

          if (this.kChart) {
            this.kChart.dispose();
          }
          this.kChart = echarts.init(this.$refs.kcontent);
          this.kChart.setOption(kline.initKOption(this.kData));

          // if (this.notInit){
          //
          //   this.kChart.setOption(kline.initKOption(this.kData) );
          //   this.notInit = false ;
          // }else {
          //   this.kChart.setOption(kline.changeKDataOption(this.kData) );
          // }

          this.isShowMA = false;
          this.isShowVOLUME = false;
          this.$nextTick(() => {
            this.isShowMA = true;
            this.isShowVOLUME = true;
          });

        });

        this.initSocket();
      },

      initSocket() {

        if (!this.ws) {
          this.ws = new ReconnectingWebSocket("ws://120.79.12.201:8080/kline/websocket?userId=" + this.userId);
          this.ws.onopen = this.onSocketEvent;
          this.ws.onclose = this.onSocketEvent;
          this.ws.onerror = this.onSocketEvent;
          this.ws.onmessage = this.onSocketMessage;
        }


        setTimeout(() => {

          let s = JSON.stringify({
            "instrument": this.instrument,
            "dateType": this.dateType,
            "userId": this.userId
          });

          console.log(s)
          this.ws.send(s);
        }, 300)


      }


    },
    created() {

      // let query  = this.$route.query;
      // this.userId =  query.userId;
      // this.token =  query.token;
      // this.instrument =  query.instrument;
      this.getFirst();

    },

    destroyed() {
      this.ws.close();
    },

    components: {},
    watch: {

      isShowMA(n, o) {
        this.kChart.setOption({
          legend: {
            selected: {
              "MA30": n,
              "MA10": n,
              "MA5": n
            }
          }
        });
        console.log(n);
      },
      isShowBOLL(n, o) {
        this.kChart.setOption({
          legend: {
            selected: {
              "BOLL_U": n,
              "BOLL_M": n,
              "BOLL_L": n
            }
          }
        });
        console.log(n);

      },
      isShowMACD(n, o) {
        if (n) {
          this.isShowKDJ = false;
          this.isShowRSI = false;
          this.isShowVOLUME = false;
          this.isShowOBV = false;

        }
        this.kChart.setOption({
          legend: {
            selected: {
              "MACD": n
            }
          }
        });
        console.log(n);
      },
      isShowKDJ(n, o) {

        if (n) {
          this.isShowMACD = false;
          this.isShowRSI = false;
          this.isShowVOLUME = false;
          this.isShowOBV = false;

        }
        this.kChart.setOption({
          legend: {
            selected: {
              "K": n,
              "D": n,
              "J": n
            }
          }
        });
        console.log(n);
      },
      isShowRSI(n, o) {
        if (n) {
          this.isShowMACD = false;
          this.isShowKDJ = false;
          this.isShowVOLUME = false;
          this.isShowOBV = false;

        }
        this.kChart.setOption({
          legend: {
            selected: {
              "RSI": n
            }
          }
        });
        console.log(n);
      },
      isShowVOLUME(n, o) {
        if (n) {
          this.isShowMACD = false;
          this.isShowKDJ = false;
          this.isShowRSI = false;
          this.isShowOBV = false;

        }
        this.kChart.setOption({
          legend: {
            selected: {
              "Volumn": n
            }
          }
        });
        console.log(n);
      },
      isShowOBV(n, o) {

        if (n) {
          this.isShowMACD = false;
          this.isShowKDJ = false;
          this.isShowRSI = false;
          this.isShowVOLUME = false;

        }
        this.kChart.setOption({
          legend: {
            selected: {
              "OBV": n
            }
          }
        });

      },
      activeM(n, o) {
        this.getFirst();
      },
      isShowPopup(n, o) {
        if (!n) {

          if (this.switchOpenName !== "") {
            let dateType = this.maData.map(item => {
              return item.name;
            }).indexOf(this.switchOpenName) + 1;
            api.saveUserPushData({

              "instrument": this.instrument,
              "dateType": dateType,
              "deviceNum": "111111111111",
              "userId": this.userId,
              "token": this.token,
              "language": "zh-cn"

            });
          }


        }
      }
    }
  };
</script>

<style scoped>

  .c-content {
    background-color: #1f212d;
    height: 100%;
    width: 100%;
  }

  .c-header {
    padding: 10px;

  }

  .c-indicator {
    padding-left: 10px;
  }

  .c-popou {

    width: 100%;
    height: 100%;
    position: fixed;
    top: 0;
    left: 0;
    background: rgba(0, 0, 0, 0.5);

  }

  .c-popou-content {
    position: absolute;
    top: 90px;
    right: 20px;
    height: auto;
    width: 100px;
    background-color: white;
  }

  .K-line {
    height: calc(97% - 126px);
    width: 100%;
  }

  .c-span_large {
    font-size: 16px;
    color: #fdfdfd;
    margin-right: 10px;
  }

  .c-span_middle {
    font-size: 14px;
    color: #fdfdfd;
    margin-right: 10px;
  }

  .c-span_large-select {
    color: #F9293E;
  }

  .c-span_price {
    font-size: 18px;
    color: #55a532;
    margin-right: 10px;
  }

  .c-div-row {
    margin-bottom: 10px;
  }

  .c-push {
    color: white;
    font-size: 12px;
    background-color: #909399;
    border-radius: 9px;
    padding: 4px 6px;
    float: right;
    margin-right: 10px;
  }
</style>



